.history {
    padding: 40px 0 60px;
    line-height: 1.2;
    &__inner {
        padding-left: 80px;
    }
    &-item {
        position: relative;
        padding: 0 0 19px 37px;
        border-left: 2px solid var(--brandColorDark);
        &:last-child {
            padding-bottom: 0;
        }
        &::before {
            position: absolute;
            content: "";
            background-color: #fff;
            border: 2px solid var(--brandColor);
            border-radius: 50%;
            width: 20px;
            height: 20px;
            top: 0;
            left: -13.5px;
        }
        h3 {
            margin: 0 0 8px;
        }
        p {
            margin: 0;
        }
    }
    &-year {
        position: absolute;
        top: 0;
        right: calc(100% + 28px);
        font-size: 18px;
        font-weight: 700;
        color: var(--iconColor);
        span {
            white-space: nowrap;
        }
    }
}
@media(max-width: 575px) {
    .history {
        &__inner {
            padding-left: 10px;
        }
        &-year {
            position: static;
            margin: 0 0 8px;
            transform: translateY(-2px);
            br {
                display: none;
            }
        }
        &-item {
            padding: 0 0 27px 28px;
        }
    }
}